import React, { Component } from 'react';
import { Mutation } from '@apollo/client/react/components';
import { SIGNIN_USER } from '../../queries';
import { withRouter } from 'react-router-dom';
import Error from '../Error';

class Signin extends Component {
	constructor(props) {
		super(props);
		this.state = {
			username: '',
			password: '',
			err: '',
		};
		this.handleChange = this.handleChange.bind(this);
		this.validateForm = this.validateForm.bind(this);
	}

	handleChange(e) {
		this.setState({ [e.target.name]: e.target.value, err: '' });
	}

	validateForm() {
		const { username, password } = this.state;
		// validate required inputs
		return !username || !password;
	}
	render() {
		const { username, password } = this.state;
		return (
			<div className='App'>
				<Mutation mutation={SIGNIN_USER}>
					{(signinUser, { data, loading, error }) => (
						<form
							className='form'
							onSubmit={async e => {
								e.preventDefault();
								// send data to server
								const user = await signinUser({
									variables: {
										username,
										password,
									},
								}).catch(e => {
									this.setState({ err: e.message });
								});
								// save token to local storage
								if (user?.data?.signinUser?.token) {
									localStorage.setItem('token', user?.data?.signinUser?.token);
									// reset current User
									await this.props.refetch();
									// clear state
									this.setState({ username: '', password: '' });
									// redirect to home page
									this.props.history.push('/');
								}
							}}>
							<h2>Sign In</h2>
							<input
								type='text'
								name='username'
								placeholder='Username'
								value={username}
								onChange={this.handleChange}
							/>
							<input
								type='password'
								name='password'
								placeholder='Password'
								value={password}
								onChange={this.handleChange}
							/>
							<button type='submit' disabled={loading || this.validateForm()}>
								Submit
							</button>
							{error ? <Error message={this.state.err} /> : ''}
						</form>
					)}
				</Mutation>
			</div>
		);
	}
}

export default withRouter(Signin);
