import React, { Component } from 'react';
import { GET_COINS } from '../../queries';
import '../../styles/coin-picker.scss';
import { useQuery } from '@apollo/client';

export class CoinSelector extends Component {
	static getCoinBySelectedText = (suggestions = [], text) => {
		const names = suggestions.map(c => c?.name?.toLowerCase());
		const idx = names.indexOf(text.toLowerCase());
		return suggestions[idx];
	};

	constructor(props) {
		super(props);
		this.state = {
			activeSuggestion: 0,
			filteredSuggestions: [],
			showSuggestions: false,
			userInput: props.defaultCoin || '',
		};
		if (props.defaultCoin) {
			const { name, asset_id } = CoinSelector.getCoinBySelectedText(props.suggestions, props.defaultCoin);
			this.updateParentWithSelectedCoin(name, asset_id);
		}
	}

	updateParentWithSelectedCoin = (name, asset_id) => {
		this.props.onClick && this.props.onClick(name, asset_id);
	};

	onChange = e => {
		const { suggestions } = this.props;
		const userInput = e.currentTarget.value;

		const filteredSuggestions = suggestions
			.filter(f => f.asset_id && f.name)
			.filter(({ asset_id, name }) => (name + asset_id).toLowerCase().indexOf(userInput.toLowerCase()) > -1);

		this.setState({
			activeSuggestion: 0,
			filteredSuggestions,
			showSuggestions: true,
			userInput: e.currentTarget.value,
		});
	};

	onFocus = e => {
		this.setState({ showSuggestions: true });
	};

	onBlur = e => {
		this.setState({ showSuggestions: false });
	};

	onClick = (name, asset_id, e) => {
		this.setState(
			{
				activeSuggestion: 0,
				filteredSuggestions: [],
				showSuggestions: false,
				userInput: name,
				name,
				asset_id,
			},
			this.updateParentWithSelectedCoin(name, asset_id),
		);
	};
	onKeyDown = e => {
		const { activeSuggestion, filteredSuggestions, name = '', asset_id } = this.state;

		if (e.keyCode === 13) {
			e.preventDefault();
			e.stopPropagation();
			const [nextName, nextAsset_id] = filteredSuggestions[activeSuggestion].split(',');
			this.setState(
				{
					activeSuggestion: 0,
					showSuggestions: false,
					userInput: nextName,
					name: nextName,
					asset_id: nextAsset_id,
				},
				this.updateParentWithSelectedCoin(nextName, nextAsset_id),
			);
		} else if (e.keyCode === 38) {
			if (activeSuggestion === 0) {
				return;
			}

			this.setState({ activeSuggestion: activeSuggestion - 1 });
		} else if (e.keyCode === 40) {
			if (activeSuggestion - 1 === filteredSuggestions.length) {
				return;
			}

			this.setState({ activeSuggestion: activeSuggestion + 1 });
		} else if (e.keyCode === 27) {
			this.setState({
				activeSuggestion: 0,
				showSuggestions: false,
				userInput: name,
			});
		}
	};

	render() {
		const {
			onChange,
			onClick,
			onKeyDown,
			state: { activeSuggestion, filteredSuggestions, showSuggestions, userInput },
		} = this;
		let suggestionsListComponent;
		if (showSuggestions && userInput) {
			if (filteredSuggestions.length) {
				suggestionsListComponent = (
					<ul className='suggestions'>
						{filteredSuggestions.map((suggestion, index) => {
							let className = null;
							const { name, asset_id } = suggestion;
							if (index === activeSuggestion) {
								className = 'marked';
							}

							return (
								<li
									key={asset_id}
									asset_id={asset_id}
									name={name}
									className={className}
									onClick={onClick.bind(this, name, asset_id)}>
									{name} - ({asset_id})
								</li>
							);
						})}
					</ul>
				);
			} else {
				suggestionsListComponent = (
					<div className='no-suggestions'>
						<em>No suggestions</em>
					</div>
				);
			}
		}

		return (
			<React.Fragment>
				<input
					placeholder='Coin'
					className='search'
					onChange={onChange}
					onKeyDown={onKeyDown}
					value={userInput}
					required={true}
				/>
				{suggestionsListComponent}
			</React.Fragment>
		);
	}
}

const CoinSelectorWrapper = props => {
	const { loading, error, data, fetchMore } = useQuery(GET_COINS);
	if (loading || !data) return <div>Loading...</div>;
	return (
		<div className='autocomplete-wrapper'>
			<CoinSelector suggestions={data.getCoins} {...props} />
		</div>
	);
};

export default CoinSelectorWrapper;
